import { ImNewspaper } from "react-icons/im";
import useConvertBufferArrayToUrl from "../../customHooks/useConvertBufferArrayToUrl";

import "./CSS/news-site.css";

const NewsSite = ({ addedNews, addedImages }) => {
  return (
    <>
      <main className="news-site">
        <div className="cover">
          <section className="large-icon">
            <div>
              <ImNewspaper className="icon" />
            </div>
          </section>

          {addedNews?.length > 0 &&
            (addedImages?.length > 0 || addedImages.length === 0) && (
              <section className="items-container">
                {addedNews
                  ?.sort((a, b) => {
                    return new Date(b?.date) - new Date(a.date);
                  })
                  .map((elements, index) => {
                    return (
                      <div
                        key={elements?._id}
                        className="news"
                        onClick={(e) => {
                          e.preventDefault();
                        }}
                      >
                        <div className="headline">
                          {elements?.headline?.toUpperCase()}
                        </div>
                        <div className="details">
                          <span className="image-container">
                            {addedImages?.length > 0 && (
                              <Image
                                alt=""
                                style={
                                  index % 2 !== 0
                                    ? { float: "left" }
                                    : { float: "right" }
                                }
                                element={
                                  addedImages[
                                    Math?.floor(
                                      Math?.random() * addedImages?.length
                                    )
                                  ]
                                }
                              ></Image>
                            )}
                          </span>
                          {elements?.details}
                        </div>
                      </div>
                    );
                  })}
              </section>
            )}

          {/* {searchResults?.length>0  &&  showNews && <section className="items-container">
                    <div className="close-button"><button onClick={()=>setSearchResults([])}>X</button></div>
                    {searchResults?.sort((a,b)=>{ return new Date(b?.datereleased) - new Date(a.datereleased)}).
                    map((elements)=>{ return (
                        <div key={elements?._id} className="individual-item" onClick={(e)=>{e.preventDefault(); setSelectedNews({news : elements}); setShowSelectedNewsModal(true)}}>
                            
                        </div>
                    )})}
                </section>} */}

          {/* <div>{!showNews && feedback}</div> */}
        </div>
      </main>
    </>
  );
};

export default NewsSite;

const Image = ({ element, style, alt }) => {
  return (
    <img
      src={useConvertBufferArrayToUrl(element)}
      alt={alt}
      style={{ ...style }}
    ></img>
  );
};
